import { BigNumberish } from '@ethersproject/bignumber';
import { Contract } from '@ethersproject/contracts';
import { Provider } from '@ethersproject/providers';
import { Transaction } from '@ethersproject/transactions';
import { Wallet } from '@ethersproject/wallet';
import { default as WethAbi } from './abi/Weth.json';
import { WETH } from './utils/constants';

export const wrapEth = async (
  amount: BigNumberish,
  wallet: Wallet
): Promise<Transaction> => {
  const instance = new Contract(WETH['1'], JSON.stringify(WethAbi), wallet);

  return instance.deposit({
    value: amount,
  });
};

export const unwrapWeth = async (
  amount: BigNumberish,
  wallet: Wallet
): Promise<Transaction> => {
  const instance = new Contract(WETH['1'], JSON.stringify(WethAbi), wallet);

  return instance.withdraw({
    value: amount,
  });
};

export const geWethMethod = (name: string, provider: Provider): any => {
    const instance = new Contract(WETH['1'], JSON.stringify(WethAbi), provider);
    return instance.estimateGas[name]
};
